@import './bootstrap-reboot.min';
@import './variables';
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');

html,
body {
  scrollbar-gutter: stable;
}

* {
    font-family: 'Roboto Condensed', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overscroll-behavior: contain;
 
}



a {
    transition: .4s all;
    &:hover {
        text-decoration: none;
    }
}

ul {
    margin-bottom: 0;
}


.container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 7px;
}

#root {
    display: flex;
    flex-direction: column;
    min-height: 100vh;

}

@keyframes sekelton {
    0% {
        opacity: 1;

    }
    50% {
        opacity: .3;

    }
    100% {
        opacity: 1;

    }
}


@keyframes charList{
    0% {
        transform: scale(0.1);
        opacity: 0;
        transform: translateX(-150%);

    }

    100% {

        transform: scale(1);
        opacity: 1;
        transform: translateX(0);

    }
}

@keyframes fadeIn{
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@media  (max-width: 1200px) {
    .container {
        max-width: 960px;
    }
    
}


@media  (max-width: 992px) {
    .container {
        max-width: 720px;
    }
    
}

@media  (max-width: 768px) {
    .container {
        max-width: 540px;
    }
    
}
@media  (max-width: 576px) {
    .container {
        max-width: 540px;
        padding: 0 20px
    }
    
}





